*{
  margin:0;
  padding:0;
}

#header {
    padding: 20px;
    background: #163991;
    color: white;
    font-size: 70px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }

  body{
      background-color: #d4d4d4;
      margin: 0;
  }

  textarea{
    resize: none;
  }

  footer {
    height: 30px;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    background-color: #163991;
    color: white;
    font-size: 16px;
    
  }

  #file{
    width: 210mm;
    top:0;
    border:solid black 1px;
    background-color: white;
    margin:12px;
    box-shadow: 5px 5px 10px;
    
    
  }
  #file.sticky {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
   
}

  #fileInput{
    min-height: 297mm;
    width: 210mm;
    border:solid black 1px;
    background-color: white;
    margin:12px;
    box-shadow: 5px 5px 10px;
    border-radius: 4px;
  }

  #main{
    margin: auto;
    width: 90%;
  }
  #flexContainer{
    align-items: center;
    display:flex;
    padding: 15px;
  }

  #fileHeader{
    width:100%;
    height: 30mm;
    background-color: #163991;
    color:white;
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    word-break: break-word;
  }

  #positionh{
    font-size: 30px;
    margin:5px;
  }

  #name{
    font-size: 45px;
    
  }

  #fileHeadInfo{
    padding:15px;
  }

#body{
  display: flex;
}

  #skills{
    flex:3.4;
    background-color: #ededed;
    height: 267mm;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    word-break: break-word;
  }

  #personalInfo{
    flex:1;
    background-color: #d4d4d4;
    max-height: 267mm;
    padding: 20px;
    display: inline-block;
    word-break: break-word;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }

  #photo{
    margin:auto;
    width:100%;
    height: 200px;
    border: 1px solid;
  }

  #contactsTitle{
    margin: 10px;
    border-bottom: 1px solid;
  }

  .info{
    padding:6px;
  }

 .label{
    color:#163991;
    width: 90%;
    border-bottom: 1px solid #163991;
    margin: 10px;
    font-size: 20px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: bold;
  }

  .field{
    width: 95%;
    padding: 12px;
    margin: 5px;
    border: 1px solid rgb(88, 88, 88);
    border-radius: 4px;
    box-sizing: border-box;
  }

  .label1{
    font-weight: bold;
    color:rgb(37, 37, 37);
    width: 95%;
    font-size: 15px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }

  .label2{
    color:#163991;
    width: 90%;
    border-bottom: 1px solid #163991;
    margin-top: 10px;
    margin-bottom: 10px;
    font-size: 15px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: bold;
  }
  .label3{
    font-weight: bold;
    color:rgb(37, 37, 37);
    width: 95%;
    font-size: 20px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    margin-bottom: 4px;
  }
  .personalInfo{
    margin: auto;
    text-align: center;
  }

  .button5 {
    background-color: white;
    color: black;
    border: 2px solid #555555;
    padding: 16px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 13px;
    margin: 4px 2px;
    transition-duration: 0.4s;
    cursor: pointer;
    margin: 20px;
  }
  
  
  .button5:hover {
    background-color: #163991;
    color: white;
    
  }
.button6 {
    background-color: rgb(196, 66, 66);
    color: black;
    border: 2px solid #555555;
    padding: 12px 28px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 13px;
    margin: 2px;
    transition-duration: 0.4s;
    cursor: pointer;
    margin: 20px;
  }

  .button6:hover {
    background-color: white;
    color: red;
    
  }
  .exp{
    padding: 10px;
    display:flex
  }

  .expfield{
    flex: 1;
  }

.input{
  margin: 25px;
}